class Service {
  // static link = "http://localhost:8000";
  static link = "https://learn-web-django01.herokuapp.com/";

  static async MethodFetch(token) {
    let detailLink = `${this.link}/api/upload/image/`;

    return await fetch(detailLink, {
      method: "GET",
      headers: {
        "Content-Type": " application/json",
        Authorization: `token ${token}`,
      },
    }).then((res) => res.json());
  }

  static async MethodCreate(valueForm, token) {
    let detailLink = `${this.link}/api/upload/image/`;

    return await fetch(detailLink, {
      method: "POST",
      headers: {
        "Content-Type": " application/json",
        Authorization: `token ${token}`,
      },
      body: JSON.stringify(valueForm),
    });
  }

  static async LoginUser(body) {
    let link = `${this.link}/api/upload/auth/`;
    return await fetch(link, {
      method: "POST",
      headers: {
        "Content-Type": " application/json",
      },
      body: JSON.stringify(body),
    }).then((resp) => resp.json());
  }

  static async RegisterUser(body) {
    let link = `${this.link}/api/upload/create/user/`;
    return await fetch(link, {
      method: "POST",
      headers: {
        "Content-Type": " application/json",
      },
      body: JSON.stringify(body),
    }).then((resp) => resp.json());
  }
}

export default Service;
