import React, { useEffect } from "react";
import { useState } from "react";
import { Link, useParams, useHistory } from "react-router-dom";
import { useCookies } from "react-cookie";

import Service from "./ApiService";
import { ItemImage } from "./ListImage";

const DetailImage = () => {
  let history = useHistory();
  const [token] = useCookies(["myToken"]);

  const params = useParams();
  const [item, setItem] = useState();

  const [fileName, setFileName] = useState("");
  const [file, setFile] = useState();

  const [success, setSuccess] = useState("");
  const [error, setError] = useState("");

  useEffect(() => {
    if (!token["myToken"]) {
      history.replace("/login/django-rest-framework");
      return;
    }
    getOne(parseInt(params.id));
  }, []);

  const handleSubmit = (e) => {
    e.preventDefault();
    if (!file && !fileName) {
      setError("fill the field");
      return;
    }

    let link = `${Service.link}/api/upload/image/${item.id}/`;

    const formData = new FormData();
    if (fileName) {
      formData.append("file_name", fileName);
    }
    if (file) {
      formData.append("myFile", file, file.name);
    }

    fetch(link, {
      method: "PUT",
      headers: {
        Authorization: `token ${token["myToken"]}`,
      },
      body: formData,
    })
      .then((res) => res.json())
      .then((res) => {
        if (res) {
          setFileName(res.file_name);
          setFile();
          setSuccess("Updated 1 image!");
          history.push("/upload-image-api/get-all");
          setItem(res);
        }
      })
      .catch((error) => {
        setError(error.message);
      });
  };

  const getOne = (id) => {
    let link = `${Service.link}/api/upload/image/${id}/`;

    fetch(link, {
      method: "GET",
      headers: {
        Authorization: `token ${token["myToken"]}`,
      },
    })
      .then((res) => res.json())
      .then((res) => {
        setItem(res);
        setFileName(res.file_name);
      })
      .catch((error) => {
        history.push("/upload-image-api/get-all");
      });
  };

  const destroy = () => {
    if (!item) {
      return;
    }
    if (!window.confirm("Are you sure ?")) {
      return;
    }

    let link = `${Service.link}/api/upload/image/${item.id}/`;
    fetch(link, {
      method: "DELETE",
      headers: {
        Authorization: `token ${token["myToken"]}`,
      },
    })
      .then((res) => {
        if (res.status === 204) {
          setSuccess("delete success");
          history.push("/upload-image-api/get-all");
        }
      })
      .catch((error) => {
        setError(error.message);
      });
  };


  return (
    <div className="container">
      <div className="my-3">
        <Link to="/upload-image-api/get-all" className="btn btn-outline-info">
          See all
        </Link>
      </div>
      <div className="row">
        <div className="col-6">
          {success && (
            <div className="alert alert-primary" role="alert">
              {success}
            </div>
          )}
          {error && (
            <div className="alert alert-danger" role="alert">
              {error}
            </div>
          )}
          <form onSubmit={handleSubmit}>
            <div className="mb-3">
              <input
                type="text"
                className="form-control"
                placeholder="set file name"
                value={fileName}
                onChange={(e) => {
                  setFileName(e.target.value);
                }}
              />
            </div>
            <div className="mb-3">
              <input
                type="file"
                accept="image/*"
                className="form-control"
                onChange={(e) => {
                  // console.log(e.target.files[0].name);
                  setFile(e.target.files[0]);
                }}
              />
            </div>
            <div className="d-flex" style={{ justifyContent: "space-between" }}>
              <button type="submit" className="btn btn-primary">
                Update
              </button>

              <button
                onClick={destroy}
                type="button"
                className="btn btn-danger"
              >
                Delete
              </button>
            </div>
          </form>
        </div>
        <div className="col-6">
          {item && <ItemImage item={item} hiddenBtn={true} />}
        </div>
      </div>
    </div>
  );
};

export default DetailImage;
