import React, { useEffect, useState, useRef } from "react";

import { useContextValue } from "../../../SocketContext";

import "./chatone.css";
import useRecorder from "./useRecorder";

const ChatOne = ({ account, fullAcc }) => {
  let [
    audioURL,
    setAudioURL,
    isRecording,
    startRecording,
    stopRecording,
    stopMediaRecorder,
  ] = useRecorder();

  const {
    receiveFile,
    receiveAudio,
    sendFile,
    sendAudio,
    sendMessage,
    receiveText,
  } = useContextValue();

  const [search, setSearch] = useState("");
  const [text, setText] = useState("");
  const [fileReceive, setFileReceive] = useState();
  const [audioReceive, setAudioReceive] = useState();
  const [rsSearch, setRsSearch] = useState([]);
  const [chooseUser, setChooseUser] = useState("user1");

  useEffect(() => {
    if (receiveText) {
      setChooseUser(receiveText["from"]);
      setText(receiveText["message"]);
    }
  }, [receiveText]);

  useEffect(() => {
    if (receiveFile) {
      setChooseUser(receiveFile["from"]);
      setFileReceive(receiveFile);
    }
  }, [receiveFile]);

  useEffect(() => {
    if (receiveAudio) {
      setChooseUser(receiveAudio["from"]);
      setAudioReceive(receiveAudio);
    }
  }, [receiveAudio]);

  // console.log(fullAcc);
  const findUser = (user) => {
    if (!user) {
      setRsSearch([]);
      return;
    }
    let rs = fullAcc.filter((acc) => acc.username.startsWith(user));
    setRsSearch(rs);
  };

  const clickChat = (item) => {
    setRsSearch([]);
    setSearch("");
    setChooseUser(item.username);
    if (account !== item.username) {
      console.log("can not choose myself");
    }
  };

  return (
    <>
      <div className="form-control" style={{ border: "none" }}>
        <input
          type="text"
          placeholder="type user name"
          className="form-control"
          value={search}
          onChange={(e) => {
            setSearch(e.target.value);
            findUser(e.target.value);
          }}
        />
        <ul className="list-group">
          {rsSearch.map((item, id) => {
            return (
              <li
                key={id}
                className="list-group-item"
                onClick={() => clickChat(item)}
              >
                {item.username} {"-"} {item.active == 0 ? "offline" : "online"}
              </li>
            );
          })}
        </ul>
      </div>
      <div>
        {chooseUser && (
          <ChatBox
            sendAudio={sendAudio}
            sendFile={sendFile}
            sendMessage={sendMessage}
            setChooseUser={setChooseUser}
            chooseUser={chooseUser}
            text={text}
            fileReceive={fileReceive}
            audioReceive={audioReceive}
            audioURL={audioURL}
            setAudioURL={setAudioURL}
            isRecording={isRecording}
            startRecording={startRecording}
            stopRecording={stopRecording}
            stopMediaRecorder={stopMediaRecorder}
          />
        )}
      </div>
    </>
  );
};

const ChatBox = ({
  audioReceive,
  sendAudio,
  sendFile,
  sendMessage,
  setChooseUser,
  chooseUser,
  text,
  fileReceive,
  audioURL,
  setAudioURL,
  isRecording,
  startRecording,
  stopRecording,
  stopMediaRecorder,
}) => {
  // text
  useEffect(() => {
    if (text.trim()) {
      clientSend(text);
    }
  }, [text]);
  // image
  useEffect(() => {
    if (fileReceive) {
      clientSendFile(fileReceive);
    }
  }, [fileReceive]);
  // audio
  useEffect(() => {
    if (audioReceive) {
      clientSendAudio(audioReceive);
    }
  }, [audioReceive]);

  const [message, setMessage] = useState("");
  const [file, setFile] = useState();

  const bodyChat = useRef();

  const hostSend = () => {
    if (message.trim()) {
      if (file) {
        sendFile(chooseUser, file);

        const blob = new Blob([file], { type: "file" });

        bodyChat.current.innerHTML += `
            <div class="mess-host-container">
            <Image class='sendImage' src=${URL.createObjectURL(
              blob
            )} fileName=${file.name} blob=${blob} />
            </div>
            `;
      } else if (audioURL) {
        sendAudio(chooseUser, audioURL);
        const blob = new Blob([audioURL], { type: "audio/ogg" });

        bodyChat.current.innerHTML += `
            <div class="mess-host-container">
              <audio src=${URL.createObjectURL(blob)} controls />    
            </div>
            `;
      }
      sendMessage(chooseUser, message);

      bodyChat.current.innerHTML += `
          <div class="mess-host-container">
          <span class="mess-item" >${message}</span>
          </div>
          `;
      setFile();
      setMessage("");
      setAudioURL("");
    }
  };

  const clientSend = (text) => {
    bodyChat.current.innerHTML += `
      <div class="mess-client-container">
        <span class="mess-item" >${text}</span>
      </div>
    `;
  };

  const clientSendFile = (fileReceive) => {
    const blob = new Blob([fileReceive.body], { type: fileReceive.type });

    bodyChat.current.innerHTML += `
        <div class="mess-client-container">
          <Image class='sendImage' src=${URL.createObjectURL(blob)} 
                fileName=${fileReceive.fileName} />
        </div>
        `;
  };

  const clientSendAudio = (audioReceive) => {
    const blob = new Blob([audioReceive.body], { type: "audio/ogg" });

    bodyChat.current.innerHTML += `
        <div class="mess-client-container">
        <audio controls>
          <source src=${URL.createObjectURL(blob)} type="audio/ogg">
        </audio>  
        </div>
        `;
  };

  const selectFile = (e) => {
    setMessage(e.target.files[0].name);
    setFile(e.target.files[0]);
  };

  return (
    <>
      <div className="card text-center mt-3 mb-3">
        <div className="card-header d-flex justify-content-between">
          <span>{chooseUser}</span>
          <button
            className="btn btn-outline-danger"
            onClick={() => {
              setChooseUser("");
              stopMediaRecorder();
            }}
          >
            exit
          </button>
        </div>
        <div
          ref={bodyChat}
          className="card-body"
          style={{ overflow: "auto", height: "300px" }}
        >
          {/* message here, ok */}
        </div>
        <div className="card-footer d-flex justify-content-evenly">
          {audioURL && <audio src={URL.createObjectURL(audioURL)} controls />}
          <button
            className="btn btn-outline-primary"
            onClick={startRecording}
            disabled={isRecording}
          >
            start recording
          </button>
          <button
            className="btn btn-outline-danger"
            onClick={() => {
              stopRecording();
              setMessage("new audio");
            }}
            disabled={!isRecording}
          >
            stop recording
          </button>
        </div>
        <div className="card-footer text-muted d-flex">
          <div className="send-img" style={{ flex: 2 }}>
            <input type="file" onChange={selectFile} />
          </div>

          <div style={{ flex: 7 }}>
            <input
              value={message}
              onChange={(e) => setMessage(e.target.value)}
              type="text"
              className="form-control"
              onKeyPress={(e) => {
                if (e.key === "Enter") {
                  if (message !== "") {
                    hostSend();
                  }
                }
              }}
            />
          </div>
          <div style={{ flex: 1 }}>
            <button onClick={hostSend} className="btn btn-outline-primary">
              send
            </button>
          </div>
        </div>
      </div>
    </>
  );
};

export default ChatOne;
