import nodejsWeb from "../asset/img/img_project/img_nodejs_web.PNG";
import djangoWeb from "../asset/img/img_project/img-django-web.PNG";
import phpWeb from "../asset/img/img_project/img_php_web.png";

// core components
// import GridContainer from "../Grid/GridContainer.js";
// import GridItem from "../Grid/GridItem.js";

// variable
const itemsProject = [
  {
    title: "Amazon Clone",
    img: amazonClone,
    tech: "Angular, FireBase",
    github: "https://github.com/giaphien363/amazon-clone",
    demo: true,
    linkDemo: "https://clone-2e34a.web.app/",
  },
  {
    title: "Flask web",
    img: flaskWeb,
    tech: "flask, postgre",
    github: "https://github.com/hiencoday363/python_flask_web",
    demo: false,
    linkDemo: "#",
  },
  {
    title: "Nodejs web",
    img: nodejsWeb,
    tech: "nodejs, sql server",
    github: "https://github.com/Allyday/mlem_mlem_sem_one",
    demo: false,
    linkDemo: "#",
  },
  {
    title: "Django web",
    img: djangoWeb,
    tech: "Django, postgre",
    github: "https://github.com/hiencoday363/django_web_01",
    demo: true,
    linkDemo: "https://learn-web-django01.herokuapp.com/",
  },
  {
    title: "PHP web",
    img: phpWeb,
    tech: "laravel, mysql",
    github: "#",
    demo: false,
    linkDemo: "#",
  },
];

const Contact = () => {
  const [feedback, setFeedback] = useState({
    username: "",
    email: "",
    subject: "",
    message: "",
  });

  const handleSubmit = (e) => {
    e.preventDefault();
    if (
      feedback.username &&
      feedback.email &&
      feedback.subject &&
      feedback.message
    ) {
      const newFeedback = { ...feedback, id: new Date().getTime().toString() };
      setFeedback({ username: "", email: "", subject: "", message: "" });
      // console.log(newFeedback);
    } else {
      alert("full field required");
    }
  };

  const changeValue = (e) => {
    const name = e.target.name;
    const value = e.target.value;

    setFeedback({ ...feedback, [name]: value });
  };

  return (
    <div className="container">
      <div>
        <div className="text-center ">
          <h2>Project</h2>
        </div>
      </div>

      <div className="gridItem mt-4 mb-4">
        {/* <GridContainer justify="center"> */}
        <div className="flex-container">
          {itemsProject.map((item, id) => {
            return (
              <div key={id} className=" flex-item col-md-4">
                {/* <GridItem xs={6} sm={6} md={4}> */}
                <ProjectItem item={item} />
                {/* </GridItem> */}
              </div>
            );
          })}
        </div>
        {/* </GridContainer> */}
      </div>

      <div style={{ marginTop: "80px" }}>
        <div className="row">
          <div className="col-md-4 mb-4">
            <h2 style={{ fontWeight: 600, marginBottom: "30px" }}>
              Get in touch
            </h2>
            <span className="font-18">Train station Ha Long</span> <br />
            <span className="font-18">Ha Long, Quang Ninh</span> <br />
            <span className="font-18">+84 388 423 182</span> <br />
            <span className="font-18">hienaloso98@gmail.com</span>
          </div>

          <div className="col-md-8">
            <form>
              <div className="row">
                <div className="col-6">
                  <input
                    required
                    className="custom-input form-control"
                    type="text"
                    placeholder="Your name"
                    name="username"
                    value={feedback.username}
                    onChange={changeValue}
                  />
                </div>
                <div className="col-6">
                  <input
                    required
                    className="form-control custom-input"
                    type="text"
                    placeholder="Your email"
                    name="email"
                    value={feedback.email}
                    onChange={changeValue}
                  />
                </div>
              </div>
              <input
                className="form-control custom-input"
                type="text"
                placeholder="Subject"
                name="subject"
                value={feedback.subject}
                onChange={changeValue}
              />
              <input
                className="form-control custom-input"
                type="text"
                placeholder="Message"
                name="message"
                value={feedback.message}
                onChange={changeValue}
              />

              <button
                onClick={handleSubmit}
                className="w-100 custom-input btn btn-secondary"
              >
                Send message
              </button>
            </form>
          </div>
        </div>
      </div>
      <Footer />
    </div>
  );
};

export default Contact;

const ProjectItem = ({ item }) => {
  // useEffect(() => {
  //   console.log(item);
  // }, []);

  return (
    <>
      <div style={{ background: "#343a40" }} className="mt-3">
        <div className="img-container">
          <img className="img-item" style={{ width: "100%" }} src={item.img} alt="" />
        </div>
        <div className="text-white styleProjectItem">
          <h5>{item.title}</h5>
          <span>Technology: {item.tech}</span>
        </div>
        <div
          className="d-flex justify-content-between"
          style={{ padding: "10px" }}
        >
          <a
            target="blank"
            href={item.github}
            className="btn btn-outline-secondary text-white"
          >
            github
          </a>
          {item.demo && (
            <a
              target="blank"
              href={item.linkDemo}
              className="btn btn-outline-secondary text-white"
            >
              demo
            </a>
          )}
        </div>
      </div>
    </>
  );
};
