import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import { CookiesProvider } from "react-cookie";

import { ContextLoginProvider } from "./LoginContext";
import { ContextProvider } from "./SocketContext";

import Header from "./components/navBar/Header";
import Home from "./components/home/Home";
import Profile from "./components/profile/Profile";
import Login from "./components/login/Login";
import Project from "./components/project/Project";

import LoginAdmin from "./components/login/LoginAdmin";
import RegisterAdmin from "./components/login/RegisterAdmin";

import LoginDjango from "./components/uploadImage/LoginDjango";
import UploadImage from "./components/uploadImage/UploadImage";
import ListImage from "./components/uploadImage/ListImage";
import DetailImage from "./components/uploadImage/DetailImage";

function App() {
  return (
    <ContextLoginProvider>
      <Router>
        <Header />
        <Switch>
          <Route exact path="/">
            <Home />
          </Route>

          <Route exact path="/project">
            <Project />
          </Route>

          <Route exact path="/function">
            <ContextProvider>
              <Profile />
            </ContextProvider>
          </Route>

          <Route exact path="/login" children={<Login />} />
          <Route exact path="/login-admin" children={<LoginAdmin />} />
          <Route exact path="/register-admin" children={<RegisterAdmin />} />

          <CookiesProvider>
            <Route
              exact
              path="/login/django-rest-framework"
              children={<LoginDjango />}
            />
            <Route
              exact
              path="/upload-image-api/get-all"
              children={<ListImage />}
            />
            <Route
              exact
              path="/upload-image-api/get/:id"
              children={<DetailImage />}
            />
            <Route
              exact
              path="/upload-image-api/upload"
              children={<UploadImage />}
            />
          </CookiesProvider>
        </Switch>
      </Router>
    </ContextLoginProvider>
  );
}

export default App;
