import React, { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";

import { useContextLogin } from "../../LoginContext";
import { useContextValue } from "../../SocketContext";

import ChatOne from "./chatnormal/ChatOne";
import ChatGroup from "./chatnormal/ChatGroup";
import ChatVideoOne from "./chatVideoOne/ChatVideoOne";
import VideoPlayerGroup from "./chatVideoGroup/VideoPlayerGroup";

// import Player from "../playMusic/Player";

// import soundAlarm from "../asset/alarm_clock.ogg";

const Profile = () => {
  let history = useHistory();

  const { userOnl, setTypedFunc } = useContextValue();

  const [typeActive, setTypeActive] = useState({
    chatOne: true,
    chatGroup: false,
    chatVideoOne: false,
    chatVideoGroup: false,
  });

  const { isLogin, account } = useContextLogin();

  const changeType = (e) => {
    if (e === "chatOne") {
      setTypeActive({
        ...typeActive,
        chatOne: true,
        chatGroup: false,
        chatVideoOne: false,
        chatVideoGroup: false,
      });

      setTypedFunc("chat");
    } else if (e === "chatVideoOne") {
      setTypeActive({
        ...typeActive,
        chatOne: false,
        chatGroup: false,
        chatVideoOne: true,
        chatVideoGroup: false,
      });
      setTypedFunc("callVideo");
    } else return;
  };

  useEffect(() => {
    if (!isLogin) {
      history.replace("/login");
    }
  }, [isLogin]);

  return (
    <div className="container">
      <div className="row mt-3">
        <div className="col-md-8">
          {!typeActive["chatVideoOne"] ? (
            <div className="d-flex justify-content-between">
              <h3>User name: {account}</h3>
              {/* <button
                onClick={() => testCase()}
                className="btn btn-outline-danger"
              >
                Back home
              </button> */}

              {/* <Player url={soundAlarm} /> */}
            </div>
          ) : (
            <>
              <div className="text-center">
                <h3>Chat video (Hin C)</h3>
              </div>
            </>
          )}
          <div className="mt-2">
            {typeActive["chatOne"] && (
              <ChatOne account={account} fullAcc={userOnl} />
            )}
            {typeActive["chatGroup"] && <ChatGroup />}
            {typeActive["chatVideoOne"] && <ChatVideoOne />}
            {typeActive["chatVideoGroup"] && <VideoPlayerGroup />}
          </div>
        </div>
        <div className="col-md-4">
          {/* load function */}
          <div className="list-group">
            <button
              type="button"
              className={
                typeActive["chatOne"]
                  ? "list-group-item list-group-item-action active"
                  : "list-group-item list-group-item-action"
              }
              onClick={() => changeType("chatOne")}
            >
              Chat one
            </button>
            <button
              type="button"
              className="list-group-item list-group-item-action"
              disabled
            >
              Chat group
            </button>
            <button
              type="button"
              className={
                typeActive["chatVideoOne"]
                  ? "list-group-item list-group-item-action active"
                  : "list-group-item list-group-item-action"
              }
              onClick={() => changeType("chatVideoOne")}
            >
              Chat video one
            </button>
            <button
              type="button"
              className="list-group-item list-group-item-action"
              disabled
            >
              Chat video group
            </button>
          </div>

          {/* load user */}
          <div className="list-group mt-5">
            <h4>List user</h4>
            {userOnl.map((item, index) => {
              return (
                <button
                  key={index}
                  type="button"
                  className="list-group-item list-group-item-action"
                >
                  {item["username"]}
                  <span className="mx-5 text-info">online</span>
                </button>
              );
            })}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Profile;
