import React, {
  createContext,
  useState,
  useRef,
  useEffect,
  useContext,
} from "react";

import { io } from "socket.io-client";
import Peer from "peerjs";
import { useHistory } from "react-router-dom";

import { useContextLogin } from "./LoginContext";
// import soundAlarm from "./components/asset/alarm_clock.ogg";
import soundAlarm from "./components/asset/alarmxperia.mp3";

const SocketContext = createContext();
// var link = "http://localhost:5005";
var link = "https://chat-video-react-v1.herokuapp.com/";
var socket = io(link, { reconnection: true });
// var socket = io(link);
var peer;

const ContextProvider = ({ children }) => {
  const { isLogin, account } = useContextLogin();
  let history = useHistory();

  const [receiveText, setReceiveText] = useState({
    to: "",
    from: "",
    message: "",
  });
  
  const [receiveFile, setReceiveFile] = useState();
  const [receiveAudio, setReceiveAudio] = useState();

  // const [peer, setPeer] = useState(new Peer());
  const [typedFunc, setTypedFunc] = useState("chat");

  const [fromUser, setFromUser] = useState("");
  const [toUser, setToUser] = useState("");

  const [inCall, setInCall] = useState(false);
  const [isReceiver, setIsReceiver] = useState(false);

  const [stream, setStream] = useState(null);
  const [userOnl, setUserOnl] = useState([]);

  const [notFound, setNotFound] = useState("");
  const [declineText, setDeclineText] = useState("");

  const [showBtnAnswer, setShowBtnAnswer] = useState(false);

  const myVideo = useRef();
  const userVideo = useRef();

  const [audio] = useState(new Audio(soundAlarm));
  const [playing, setPlaying] = useState(false);

  useEffect(() => {
    if (typedFunc === "chat") {
      return;
    }
    playing ? audio.play() : audio.pause();
  }, [typedFunc, playing]);

  useEffect(() => {
    if (typedFunc === "chat") {
      return;
    }
    audio.addEventListener("ended", () => {
      setPlaying(false);
    });
    return () => {
      audio.removeEventListener("ended", () => setPlaying(false));
    };
  }, [typedFunc]);

  useEffect(() => {
    if (typedFunc === "chat") {
      return;
    }

    if (showBtnAnswer) {
      setPlaying(true);
      let timeOut = setTimeout(() => {
        setPlaying(false);

        decline();

        clearTimeout(timeOut);
      }, 30000);
    } else {
      setPlaying(false);
    }
  }, [typedFunc, showBtnAnswer]);

  useEffect(() => {
    if (!socket.connected) {
      socket.connect();
    }

    if (!isLogin) {
      history.replace("/");
    } else {
      if (typedFunc === "chat") {
        startChat();

        socket.on("updateUser_chat", (data) => {
          setUserOnl(data);
        });
        socket.on("receiveMess", (data) => {
          setReceiveText(data);
        });
        
        socket.on("receiveFile", (data) => {
          setReceiveFile(data);
        });
        
        socket.on("receiveAudio", (data) => {
          setReceiveAudio(data);
        });

      } else if (typedFunc === "callVideo") {
        socket.disconnect();
        socket = io(link, { forceNew: true });

        startPeer();
        if (!stream) {
          getStream().then((currentStream) => {
            setStream(currentStream);
            myVideo.current.srcObject = currentStream;
          });
        }

        socket.on("calling", (from) => {
          if (from) {
            setFromUser(from);
            setShowBtnAnswer(true);
          }
        });

        socket.on("updateUser", (data) => {
          setUserOnl(data);
        });
      }
    }

    return () => {
      exit();
      socket.connect();
    };
  }, [typedFunc]);

  const startChat = () => {
    socket.emit("user_connected_chat", { userLogIn: account });
  };

  const sendMessage = (toUser, message) => {
    if (!socket.connected) {
      socket.connect();
    }
    socket.emit("sendMess", { to: toUser, from: account, message });
  };

  const sendFile = (toUser, file) => {
    let messageObj = {
      to: toUser,
      from: account,
      type: "file",
      body: file,
      mimeType: file.type,
      fileName: file.name,
    };
    socket.emit("sendFile", messageObj);
  };

  const sendAudio=(toUser, audioURL)=>{
    let messageObj = {
      to: toUser,
      from: account,
      type: "audio",
      body: audioURL,
    };
    socket.emit("sendAudio", messageObj);
  }

  const getStream = async () => {
    return await navigator.mediaDevices.getUserMedia({
      video: true,
      audio: true,
    });
  };

  const startPeer = () => {
    peer = new Peer();

    peer.on("open", () => {
      socket.emit("user_connected", { peer_id: peer.id, userLogIn: account });
    });
  };

  // const testCase = () => {
  // console.log("socket connect:", socket.connected);
  // socket.emit("get_user");

  //   console.log("user online", userOnl);
  // };

  const callUser = () => {
    if (toUser.length <= 0 || inCall) return;

    socket.emit("callUser", { to: toUser, from: account });

    socket.on("notFound", (data) => {
      setNotFound(data["response"]);

      let timeOut = setTimeout(() => {
        setNotFound("");
        clearTimeout(timeOut);
      }, 3000);
    });

    socket.on("callAccepted", (data) => {
      if (data["accept"]) {
        var call = peer.call(data["peer_id"], stream);

        call.on("stream", function (remoteStream) {
          setInCall(true);
          userVideo.current.srcObject = remoteStream;
        });

        call.on("close", () => {
          setInCall(false);
          peer.destroy();

          startPeer();
        });
      } else {
        setDeclineText("The call was rejected");
        let timeOut = setTimeout(() => {
          setDeclineText("");
          clearTimeout(timeOut);
        }, 3000);
      }
    });

    socket.on("endCall", (data) => {
      if (data["endCall"]) {
        setInCall(false);
        peer.destroy();

        startPeer();
        setFromUser("");
        setToUser("");
      }
    });
  };

  // always listen call
  const listenAnswer = () => {
    peer.on("call", (call) => {
      if (inCall) return;

      call.answer(stream); // Answer the call with an A/V stream.
      call.on("stream", function (remoteStream) {
        setInCall(true);
        userVideo.current.srcObject = remoteStream;
      });

      call.on("close", () => {
        setInCall(false);
        peer.destroy();

        startPeer();
      });
    });
  };

  const answer = () => {
    socket.emit("answerCall", { accept: true, to: fromUser, from: account });
    listenAnswer();
    setShowBtnAnswer(false);
    setIsReceiver(true);

    socket.on("endCall", (data) => {
      if (data["endCall"]) {
        setInCall(false);
        peer.destroy();

        startPeer();
        setFromUser("");
        setToUser("");
      }
    });
  };

  const decline = () => {
    socket.emit("answerCall", { accept: false, to: fromUser });
    setShowBtnAnswer(false);
  };

  const hangup = () => {
    if (inCall) {
      setInCall(false);
      peer.destroy();

      startPeer();
      if (isReceiver) {
        socket.emit("leaveCall", { to: fromUser });
      } else {
        socket.emit("leaveCall", { to: toUser });
      }
      setFromUser("");
      setToUser("");
    }
  };

  const exit = () => {
    if (stream) {
      stream.getTracks().forEach(function (track) {
        if (track.readyState === "live") {
          track.stop();
        }
      });
    }

    if (peer) {
      peer.destroy();
    }

    if (inCall) {
      setInCall(false);
      if (isReceiver) {
        socket.emit("leaveCall", { to: fromUser });
      } else {
        socket.emit("leaveCall", { to: toUser });
      }
    }

    setFromUser("");
    setToUser("");

    socket.emit("endSession");
  };

  const turnOffCam = () => {
    if (stream) {
      stream.getTracks().forEach(function (track) {
        if (track.readyState === "live" && track.kind === "video") {
          track.enabled = !track.enabled;
        }
      });
    }
  };

  const turnOffMic = () => {
    if (stream) {
      stream.getTracks().forEach(function (track) {
        if (track.kind === "audio" && track.readyState === "live") {
          track.enabled = !track.enabled;
        }
      });
    }
  };

  return (
    <SocketContext.Provider
      value={{
        userOnl,
        declineText,
        notFound,
        fromUser,
        toUser,
        inCall,
        stream,
        showBtnAnswer,
        myVideo,
        userVideo,
        setToUser,
        callUser,
        answer,
        decline,
        hangup,
        turnOffCam,
        turnOffMic,
        // testCase,
        setTypedFunc,
        exit,
        // chat
        sendMessage,
        sendFile,
        receiveAudio,
        sendAudio,
        receiveText,
        receiveFile
      }}
    >
      {children}
    </SocketContext.Provider>
  );
};

const useContextValue = () => useContext(SocketContext);

export { ContextProvider, useContextValue };
