import React, { useEffect, useRef, useState } from "react";
import { Link, useHistory } from "react-router-dom";
import { useCookies } from "react-cookie";

import Service from "./ApiService";

import "./UploadImage.css";

const UploadImage = () => {
  let history = useHistory();
  const [token] = useCookies(["myToken"]);

  const [fileName, setFileName] = useState("");
  const [file, setFile] = useState(null);

  const form = useRef(null);

  const [success, setSuccess] = useState("");
  const [error, setError] = useState("");

  useEffect(() => {
    if (!token["myToken"]) {
      history.replace("/login/django-rest-framework");
      return;
    }
  }, []);

  const handleUpload = (e) => {
    e.preventDefault();
    if (!file || !fileName) {
      setError("fill the field");
      return;
    }

    const formData = new FormData();
    formData.append("file_name", fileName);
    formData.append("myFile", file, file.name);

    fetch(`${Service.link}/api/create/image/`, {
      method: "POST",
      headers: {
        Authorization: `token ${token["myToken"]}`,
      },
      body: formData,
    })
      .then((res) => {
        if (res.status === 201) {
          setFileName("");
          setFile();
          setSuccess("Uploaded 1 image!");
          let countTime = setTimeout(() => {
            history.push("/upload-image-api/get-all");
            clearTimeout(countTime);
          }, 1500);
        }
      })
      .catch((error) => {
        setError(error.message);
      });
  };

  return (
    <div className="container">
      <div className="my-3">
        <Link to="/upload-image-api/get-all" className="btn btn-outline-info">
          See all
        </Link>
      </div>
      <br />
      {success && (
        <div className="alert alert-primary" role="alert">
          {success}
        </div>
      )}
      {error && (
        <div className="alert alert-danger" role="alert">
          {error}
        </div>
      )}
      <form ref={form} onSubmit={handleUpload} encType="multipart/form-data">
        <div className="mb-3">
          <input
            type="text"
            className="form-control"
            placeholder="set file name"
            value={fileName}
            onChange={(e) => {
              setFileName(e.target.value);
            }}
          />
        </div>
        <div className="mb-3">
          <input
            type="file"
            accept="image/*"
            className="form-control"
            onChange={(e) => {
              setFile(e.target.files[0]);
            }}
          />
        </div>

        <button type="submit" className="btn btn-primary">
          Upload new image
        </button>
      </form>
    </div>
  );
};

export default UploadImage;
