import React, { useState, useEffect, useRef } from "react";
import { useCookies } from "react-cookie";
import { useHistory } from "react-router-dom";

import Service from "./ApiService";

const LoginDjango = () => {
  const [valueForm, setValueForm] = useState({
    username: "",
    password: "",
  });
  const [token, setToken] = useCookies(["myToken"]);
  let history = useHistory();

  const [isLogIn, setIsLogIn] = useState(true);
  const [isLoad, setIsLoad] = useState(false);

  const [error, setError] = useState("");

  const repeatPass = useRef(null);

  useEffect(() => {
    document.title = "Login Django Rest Framework";
    // if (token["myToken"]) {
    //   history.replace("/upload-image-api/get-all");
    // }
  }, []);

  const changeValue = (e) => {
    const name = e.target.name;
    const value = e.target.value;
    setValueForm({ ...valueForm, [name]: value });
  };

  const handleSubmit = (e) => {
    setIsLoad(true);
    e.preventDefault();
    if (valueForm.username.length > 4 && valueForm.password.length > 4) {
      Service.LoginUser(valueForm)
        .then((resp) => {
          if (resp.token) {
            setToken("myToken", resp.token);
            history.push("/upload-image-api/get-all");
          } else {
            setError(resp.non_field_errors[0]);
          }
        })
        .catch((err) => {
          setError(err.message);
        });
    } else {
      setError("Invalid form, please type more");
    }
    setIsLoad(false);
  };

  const register = (e) => {
    setIsLoad(true);
    e.preventDefault();
    let temp = repeatPass.current.value;
    if (
      valueForm.username.length > 4 &&
      valueForm.password.length > 4 &&
      valueForm.password === temp
    ) {
      Service.RegisterUser(valueForm)
        .then((resp) => {
          if (resp.username && resp.id) {
            Service.LoginUser(valueForm).then((resp) => {
              setToken("myToken", resp.token);
              history.push("/upload-image-api/get-all");
            });
          }
          setError(resp);
        })
        .catch((err) => {
          setError(err.message);
        });
    } else {
      setError("Invalid form, please type more");
    }
    setIsLoad(false);
  };

  return (
    <React.Fragment>
      <div className="mt-4 col-md-6 mx-auto">
        <div className="text-center">
          <h3>{isLogIn ? "Log In" : "Register"} page</h3>
        </div>

        {error && (
          <div className="alert alert-danger my-3" role="alert">
            {error}
          </div>
        )}

        <form onSubmit={handleSubmit}>
          <div className="mb-3">
            <label htmlFor="exampleInputEmail1" className="form-label">
              User name
            </label>
            <input
              type="text"
              className="form-control"
              id="exampleInputEmail1"
              name="username"
              value={valueForm.username}
              onChange={changeValue}
            />
          </div>

          <div className="mb-3">
            <label htmlFor="exampleInputPassword1" className="form-label">
              Password
            </label>
            <input
              type="password"
              className="form-control"
              id="exampleInputPassword1"
              name="password"
              value={valueForm.password}
              onChange={changeValue}
            />
          </div>

          {isLogIn || (
            <div className="mb-3">
              <label className="form-label">Repeat password</label>
              <input
                type="password"
                className="form-control"
                ref={repeatPass}
              />
            </div>
          )}

          {isLogIn ? (
            <button disabled={isLoad} type="submit" className="btn btn-primary">
              Log in
            </button>
          ) : (
            <button
              disabled={isLoad}
              type="button"
              onClick={register}
              className="btn btn-primary"
            >
              Register
            </button>
          )}

          {isLogIn ? (
            <div className="my-3">
              If you dont have any account, click{" "}
              <span
                style={{ cursor: "pointer" }}
                className="text-info"
                onClick={() => setIsLogIn(!isLogIn)}
              >
                here
              </span>
            </div>
          ) : (
            <div className="my-3">
              If you have an account, click{" "}
              <span
                style={{ cursor: "pointer" }}
                className="text-info"
                onClick={() => setIsLogIn(!isLogIn)}
              >
                here
              </span>
            </div>
          )}
        </form>
      </div>
    </React.Fragment>
  );
};

export default LoginDjango;
