import React, { useEffect, useState } from "react";
import { Link, useHistory } from "react-router-dom";
import { useCookies } from "react-cookie";

import Service from "./ApiService";

const ListImage = () => {
  const [token, setToken, removeToken] = useCookies(["myToken"]);
  let history = useHistory();

  const [listImage, setListImage] = useState([]);
  const [notice, setNotice] = useState("");

  const download = async () => {
    await Service.MethodFetch(token["myToken"])
      .then((res) => {
        if (res.length <= 0) {
          setNotice("No record");
        } else {
          setNotice("");
        }
        setListImage(res);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  const logOut = () => {
    history.push("/");
    removeToken("myToken");
  };

  useEffect(() => {
    if (!token["myToken"]) {
      history.replace("/login/django-rest-framework");
      return;
    }
    download();
  }, []);

  return (
    <div className="container">
      <div className="my-3">
        <Link className="btn btn-outline-primary" to="/upload-image-api/upload">
          create
        </Link>
        {token["myToken"] && (
          <button className="mx-3 btn btn-outline-danger" onClick={logOut}>
            Log out
          </button>
        )}
      </div>
      <div className="d-flex col-12 my-3" style={{ flexWrap: "wrap" }}>
        {notice && <h1>{notice}</h1>}
        {listImage.map((item, id) => {
          return (
            <div className="col-3" key={id}>
              <ItemImage item={item} hiddenBtn={false} />
            </div>
          );
        })}
      </div>
    </div>
  );
};

export const ItemImage = ({ item, hiddenBtn }) => {
  return (
    <div className="card" style={{ width: "90%", marginBottom: "20px" }}>
      <div style={{ height: "250px" }}>
        <img
          src={`${Service.link}${item.myFile}`}
          className="card-img-top"
          alt="..."
          style={{ objectFit: "cover", height: "100%" }}
        />
      </div>
      <div className="card-body">
        <h5 className="card-title">
          {item.id}: {item.file_name}
        </h5>
        <p className="card-text">
          Lorem ipsum dolor sit amet consectetur adipisicing elit.
        </p>
        {hiddenBtn || (
          <Link
            to={`/upload-image-api/get/${item.id}`}
            className="btn btn-primary"
          >
            Detail
          </Link>
        )}
      </div>
    </div>
  );
};

export default ListImage;
